import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Modal from 'react-modal';
import axios from 'axios';
import { useState,useEffect } from 'react';
import { Xl, XLg } from 'react-bootstrap-icons';
import './Navbar2.css';
import '../login/LoginModal.css';
import { Link, useHistory } from 'react-router-dom';
import { ADDRESS } from '../../Adress';

Modal.setAppElement('#root');

const Navbar2=() =>{
  const [userId,setUserId] = useState('');
  const [password,setPassword] = useState('');
  const history = useHistory();

  {/**   */}
  const [modalIsOpen, setModalIsOpen] = useState(false);


  {/**  */}
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const storedLoginStatus = localStorage.getItem('isLoggedIn');
    setIsLoggedIn(storedLoginStatus === 'true');
  }, []);
  
  {/**      true  */}
  const handleModalOpen = () => {
    setModalIsOpen(true);
  };

  {/**      false  */}
  const handleModalClose = () => {
    setModalIsOpen(false);
  };



  {/**  */}
  const handleLogout = () => {
    console.log(isLoggedIn);
    localStorage.removeItem("isLoggedIn")
    setIsLoggedIn(false);
    //    
    localStorage.removeItem("token");
    localStorage.removeItem('id');
    localStorage.removeItem('userId');
    
    //     
    history.push("/");
    alert(' ')
   
  };

  

  const handleLogin = async (event) => {
    event.preventDefault();
    
    try {
      console.log(JSON.stringify({ userId, password }))
      const response = await axios.post(`${ADDRESS}/login`, {
        userId,
        password
      }, {
        headers: {
          'Content-Type': 'application/json'
        }
      });
      const token = response.data.JWT; // JWT  .
      const id = response.data.Id;
      console.log(response);
      localStorage.setItem('token', token); //     .
      localStorage.setItem("userId",userId);
      localStorage.setItem("id",id);
      handleModalClose();
      setIsLoggedIn(true);
      localStorage.setItem("isLoggedIn",'true')
      alert(' .')
      
      console.log(response)
      if (response.data.userRole=== 'ADMIN') { //   .
        window.location.pathname = '/admin'}
      
    } catch (error) {
      //    
        setUserId('')
        setPassword('')
        console.log(error)
        alert('  ');
     
      }
    };
  


  return (
    <>
    
      <Navbar collapseOnSelect className='navbar'>
        <Container id='content'>
          <Navbar.Brand href='/' id='logo'>
             
          </Navbar.Brand>
          <Navbar.Toggle aria-controls='responsive-navbar-nav' />
          <Navbar.Collapse id='responsive-navbar-nav'>
            <Nav className='me-auto'>
              <Nav.Link href='/post'></Nav.Link>
            </Nav>
            <Nav>
              <Nav.Link href='/mypage'></Nav.Link>
             
              <Nav.Link onClick={() => {
               
                  if (isLoggedIn) {
                    handleModalClose();
                  } else {
                    handleModalOpen();
                  }
                  
                }}>
                   {isLoggedIn ? (
                    <button onClick={handleLogout}></button>
                  ) : (
                    <button></button>
                  )}
                              
              </Nav.Link>

            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      {/*  */}
      <Modal isOpen={modalIsOpen} onRequestClose={handleModalClose} 
    style={{
        zIndex:10,
        overlay: {
            backgroundColor: 'rgba(0, 0, 0, 0.5)', //   
          },
        content: {
          width: '30%', //    
          height: '55%', //    
          margin: 'auto', //      margin
          borderRadius:'20px' //  

        },
        
       
      }}>

      <div style={{textAlign:'right'}}>
      <button style={{backgroundColor:'white',padding:'0px'}} onClick={handleModalClose}><XLg/></button>
      </div>

      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <div className='login' style={{ margin: '0 auto'}}> </div>
        
      </div>
            
      <div className='login_button'>

      <div>

        <form onSubmit={handleLogin} style={{display:'flex',flexDirection:'column',alignItems:'center',justifyContent:'center'}}>

        <div> <input type='text' value={userId}
          onChange={(event) => setUserId(event.target.value)} placeholder='' style={{marginTop:'15px', padding:'10px', borderRadius:'5px', border:'1px solid #ccc'}} /></div>

        <div><input type='password' value={password}
          onChange={(event) => setPassword(event.target.value)} placeholder='' style={{marginTop:'15px', padding:'10px', borderRadius:'5px', border:'1px solid #ccc'}} /></div>

       
        
        <div style={{marginTop:'40px'}}><button type="submit" ></button></div>
        </form>

      </div>

    

      <div style={{marginTop:'30px'}}>
      <Link to='/signup' onClick={handleModalClose}>  ?</Link>
     
      </div>


      </div>

    </Modal>
    </>
  );
}

export default Navbar2;
