import axios from 'axios';
import React, { useState } from 'react'
import { Display } from 'react-bootstrap-icons';
import { useHistory } from 'react-router-dom';
import { ADDRESS } from '../Adress';

import './Signup.css';

function Signup() {
    const [userName,setUserName] = useState('');
    const [userId,setUserId] = useState('');
    const [password,setPassword] = useState('');
    const [userMood,setuserMood] = useState('');
    const [isUserIdValid,setIsUserIdValid]=useState(false);
    const history = useHistory();


    //
    const overlapCheack=async (event)=>{
      event.preventDefault();
      try {
        console.log(JSON.stringify({ userId }))
        const response = await axios.get(`${ADDRESS}/id`, {
          params: { userId: userId }
         
        }, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        if(response.data==true){
          alert('    .')
          setIsUserIdValid(true); //     .
        }else{
          alert('  .')
          setIsUserIdValid(false); //     .
          setUserId('')

        }
        
      
      } catch (error) {
        console.error(error);
      }
    };
  



  const handleSignup = async (event) => {
    event.preventDefault();
    if (!userId || !password || !userName || !userMood) {
      alert('   .');
      return;
    }

    if (!isUserIdValid) {
      alert('   .');
      return;
    }

    try {
      console.log(JSON.stringify({ userId, password,userName ,userMood }))
      const response = await axios.post(`${ADDRESS}/join`, {
        userId,
        password,
        userName,
        userMood,
 
      }, 
      {
        headers: {
          'Content-Type': 'application/json'
        }
      });
      console.log(response.data);
      alert(response.data);
      history.push('/')
    } catch (error) {
      console.error(error);
    }
  };


    //  
    const handleMoodChange = (event) => {
      setuserMood(event.target.value); //    
      
    }
      

  return (
    <div className="background-container" style={{height:'550px'}}>
      
        <div className="overlay-container">

            <div style={{
            fontWeight:"bold",
            fontSize:"large",
            marginRight:"40px",
            marginLeft:"40px",
            borderBottom: '1px solid gray'
            }}>
            </div>

            <form onSubmit={handleSignup}>
                <div style={{textAlign:'center', marginTop:'70px'}}>
                <div><input value={userName} onChange={(event) => setUserName(event.target.value)} className='input' type='text' placeholder=' .'></input></div>
                <div><input value={userId} onChange={(event) => setUserId(event.target.value)} className='input' type='text' placeholder=' .' style={{width:'320px'}}></input>
                <span style={{marginLeft:'20px'}}><button onClick={overlapCheack}></button></span></div>
                <div><input value={password} onChange={(event) => setPassword(event.target.value)} className='input' type='password' placeholder=' .'></input></div>
                   

                    <div style={{marginTop:'30px',color:'#1E90FF',fontWeight:'bold'}}>    </div>
                    <div style={{display:'flex',justifyContent:'center',marginTop:'30px'}}>
                    
                        <div>
                        <input type='radio' id='hip' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                        <label htmlFor='hip'  className={userMood === '' ? 'userMood selected' : 'mood'}></label>
                        </div>
                        <div>
                        <input type='radio' id='healing' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                        <label htmlFor='healing' className={userMood === '' ? 'userMood selected' : 'mood'}></label>
                        </div>
                        <div>
                        <input type='radio' id='retro' name='mood' value='' onChange={handleMoodChange}  style={{display:'none'}} />
                        <label htmlFor='retro' className={userMood === '' ? 'userMood selected' : 'mood'}></label>
                        </div>
                        <div>
                        <input type='radio' id='romantic' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                        <label htmlFor='romantic' className={userMood=== '' ? 'userMood selected' : 'mood'}></label>
                        </div>
                        <div>
                        <input type='radio' id='active' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                        <label htmlFor='active' className={userMood=== '' ? 'userMood selected' : 'mood'}></label>
                        </div>
                </div>

                <div style={{marginTop:'40px'}}><button   style={{marginTop:'15px', padding:'10px', borderRadius:'5px', border:'1px solid #ccc',width:'400px'}} 
                type="submit" ></button></div>
                </div>
            </form>
        </div>
    </div>
  )
}

export default Signup
