import React, { useEffect } from 'react'
import { useState,useContext } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import ListGroup from 'react-bootstrap/ListGroup';
import { PersonCircle } from 'react-bootstrap-icons';
import './Mypage.css'
import axios from 'axios';
import { ADDRESS } from '../../Adress';


function Mypage() {
  
  const [data,setData]=useState({});
  const token = localStorage.getItem('token');
  const id = localStorage.getItem('id');
  useEffect(() => {
   
  
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  
    axios.get(`${ADDRESS}/users/${id}`)
      .then(response => {
        //    
        console.log(response.data);
        setData(response.data);

      })
      .catch(error => {
        //  
        console.error(error);
      });
      
  }, []);
  
  


  if (!token) {
    //      
    alert('   .')

    return null;
  }


  

  return (
    <div>
      <div style={{display:'flex'}}>
      <div style={{width:'250px',margin:'30px 30px'}}>
         <ListGroup defaultActiveKey='/mypage'>
          <ListGroup.Item action href='/mypage'>
            
          </ListGroup.Item>
          <ListGroup.Item action href='/courses'>
              
          </ListGroup.Item>
          <ListGroup.Item action href='/scraps'>
             
          </ListGroup.Item>
        </ListGroup>
      </div>
      <div className='background-container' id='mypage_background'>
       <div className='overlay-container'>

       <div style={{
        fontWeight:"bold",
        fontSize:"large",
        marginRight:"40px",
        marginLeft:"40px",
        height:'50px',
        borderBottom: '1px solid gray'
        }}> 
      </div>
      <div style={{display:'flex'}}>
      <PersonCircle fontSize={100} style={{margin:'60px 50px'}}color='gray'/>
     <div style={{margin:"40px 20px"}}>
      <div  style={{marginBottom: "10px"}}>  : {data.userName}</div>
      <div  style={{marginBottom: "10px"}}>  : {data.userId}</div>
      <div  style={{marginBottom: "10px"}}>   : {data.userMood} <button className='reBtn'></button></div>
      </div>
      </div>
      <div style={{margin:"50px 50px"}}>
       <button className='delBtn'> 
       
       </button>
      </div>

    </div>    
      </div>
      </div>

    </div>
    
  )
}

export default Mypage;
